import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { GetDataGroup, hideModal } from "../../../actions/datamaster_action";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import ValidasiParamterPoint from "../../../Validasi/ValidasiParamterPoint";
// import ValidasiMasterSales from "../../../Validasi/ValidasiMasterSales.jsx";
import Loading from 'react-fullscreen-loading';
import { Gramasi, NumberOnly } from "../../../Validasi/normalize";
import { createNumberMask } from "redux-form-input-masks";
import { getData2 } from "../../../config/axios";

const maptostate = (state) => {
  if (state.datamaster.ShowModalParameterPoint !== null) {
    //   console.log(state.datamaster.ShowModalParameterPoint);
    return {
      datagroup: state.datamaster.GetDataGroup,
      initialValues: {
        kode_group: state.datamaster.ShowModalParameterPoint.kode_group,
        nominal: state.datamaster.ShowModalParameterPoint.nominal,
        poin: state.datamaster.ShowModalParameterPoint.poin,
      },
    };
  }
};
const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: " ,-",
  locale: "kr-KO",
});
class ModalParamterPoint extends Component {
  state = {
    aktif: false,
  };
  componentDidMount() {
    this.props.dispatch(GetDataGroup());
    setTimeout(() => {
      this.props.isEdit
        ? document.getElementById("berat").focus()
        : document.getElementById("kode_group").focus();
    }, 100);
    getData2("para-system/key/TIPE_POIN").then((res) => {
      if (res.data.value === "RP") {
        this.setState({
          kategori_harga: true,
        });
      } else {
        this.setState({
          kategori_harga: false,
        });
      }
    });
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <div className="row">
          <div className="col-6">
            <Field
              id="kode_group"
              label="Kode Group"
              name="kode_group"
              options={this.props.datagroup
                .filter((list) => list.kode_group !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_group,
                    name: list.kode_group + " - " + list.nama_group,
                  };
                  return data;
                })}
              value={this.props.kode_group}
              disabled={this.props.isEdit}
              placeholder={"Silahkan Pilih Kode Group"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-6">
          {this.state.kategori_harga ? (
              <Field
                id="berat"
                name="nominal"
                component={ReanderField}
                type="text"
                // normalize={Gramasi}
                {...currencyMask}
                label="Nominal"
                placeholder="Masukan Nominal"
              />
            ) : (
              <Field
                id="berat"
                name="nominal"
                component={ReanderField}
                type="text"
                normalize={Gramasi}
                label="Gram"
                placeholder="Masukan Gram"
              />
            )}
          </div>
          <div className="col-12">
            <Field
              id="poin"
              name="poin"
              component={ReanderField}
              type="text"
              normalize={NumberOnly}
              label="Poin"
              placeholder="Masukan Poin"
              onBlur={() => this.setState({ aktif: true })}
            />
          </div>
          <div className="col-12 text-right">
          &nbsp;
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
          <button
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
            
           
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

ModalParamterPoint = reduxForm({
  form: "ModalParamterPoint",
  enableReinitialize: true,
  validate: ValidasiParamterPoint,
})(ModalParamterPoint);
export default connect(maptostate, null)(ModalParamterPoint);
