import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelBody, PanelHeader } from "../../../components/panel/panel.jsx";

import {
  showModal,
  hideModal,
  getParameterPoint,
  ShowModalParameterPoint,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import { getData2, postData, putData } from "../../../config/axios.jsx";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import ModalParamterPoint from "./ModalParamterPoint.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
const maptostate = (state) => {
  return {
    ParameterPoint: state.datamaster.getParameterPoint,
    hideModal: state.datamaster.modalDialog,
  };
};
class FormParamterPoint extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      type_poin : "GRAM",
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getParameterPoint());
    getData2("para-system/key/TIPE_POIN").then((res) => {
      // console.log(res.data);
      this.setState({
        type_poin : res.data.value === "GRAM" ? "GRAM" : "RP"
      })
    })
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });

    let data_post = {
      kode_group: data.kode_group,
      nominal: data.nominal,
      poin: data.poin,
    };
    this.state.isEdit
      ? putData("parapoin/edit/"+data.kode_group, data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Diedit");
          })
          .then(() => {
            this.props.dispatch(getParameterPoint());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalParamterPoint"));
          })
          .catch((error) => {
            ToastNotification("info", "Terjadi Kesalahan Saat Menyimpan Data");
            this.setState({
              isLoading: true,
            });
          })
      : postData("parapoin/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Simpan");
          })
          .then(() => {
            this.props.dispatch(getParameterPoint());
          })
          .then(() => {
            this.props.dispatch(reset("ModalParamterPoint"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "sales/reactive/by-kode-sales/",
              data.kode_sales,
              data_post,
              this.props.dispatch,
              getParameterPoint(),
              hideModal(),
              "ModalParamterPoint"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }
  formEditParameterPoint(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalParameterPoint(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  showModalSales() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalParameterPoint(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    const columns = [
      {
        dataField: "kode_group",
        text: "Kode Group",
        sort: true,
      },
      {
        dataField: "nominal",
        text: this.state.type_poin === "GRAM" ? "Gram" : "Rp",
        headerClasses : "text-right",
        formatter: (value, row) => {
          return <div className="text-right"> {value.toLocaleString("ko-KO") || 0} </div>;
        },
      },
      {
        dataField: "poin",
        text: "Poin",
        headerClasses : "text-right",
        formatter: (value, row) => {
          return <div className="text-right"> {value || 0} </div>;
        },
      },
      {
        dataField: "action",
        text: "Action",
        csvExport: false,
        headerClasses: "text-center",
        formatter: (rowcontent, row) => {
          let dataEdit = {
            kode_group: row.kode_group,
            nominal: row.nominal,
            poin: row.poin,
          };
           
          return (
            <div className="row text-center">
              <div className="col-12">
                <button
                  onClick={() => this.formEditParameterPoint(dataEdit)}
                  className="btn btn-primary mr-3"
                >
                  {" "}
                  <i className="fa fa-edit"></i>
                </button>
              </div>
            </div>
          );
        },
      },
      ]
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Parameter Point</li>
        </ol>
        <h1 className="page-header">Parameter Point </h1>
        <Panel>
          <PanelHeader>Parameter Point</PanelHeader>
          <br />

          {/* Parameter Pint */}
          <PanelBody>
            <Tabel
              keyField="kode_group"
              tambahData={true}
              handleClick={() => this.showModalSales()}
              exportCsv={true}
              data={this.props.ParameterPoint}
              columns={columns}
              empty={this.props.ParameterPoint}
              textEmpty="Parameter Point Kosong"
            />
          </PanelBody>
          <br />
          {/* End Master Sales */}
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit
                ? "Edit Parameter Point"
                : "Tambah Parameter Point"
            }
            content={
              <ModalParamterPoint
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                type_poin={this.state.type_poin}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* <Modal  title={ this.state.isEdit ? 'Edit Parameter Point' : 'Tambah Parameter Point' } content={ <FormModalKategori isEdit={this.state.isEdit} /> } isOpen={ this.props.hideModal} onSubmit={ (data)=>this.handleSubmit(data) } /> */}

          {/* End Tambah Master Sales  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(FormParamterPoint);
